// * Responsive

$mobile-max: 430px;
$mobile-max-dev: 830px;
$mobile-min: 0px;

@mixin ëª¨ë°”ì¼ {
  @media screen and (min-width: #{$mobile-min}) and (max-width: #{$mobile-max-dev}) {
    @content;
  }
}

// ----------------------------------------------------------------------------------------
// * Adaptive
// device: "mobile" | "desktop" 
@mixin device ($device) {
  @at-root .#{$device} {
    @content;
  }
}